.footer {
    width: 100%;
    height: 400px;
    background-color: rgb(69, 69, 69);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

}

.footer-callout {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100%;

}
.footer-callout > h1{
    font-size: 30px;
    color: white;
    font-weight: bold;
    font-family: sans-serif;
    margin: 0px;
    margin-top: 25px;
    margin-bottom: 5px;
    padding: 0px;
}
.footer-callout > h2 {
    font-size: 20px;
    color: #CFB991;
    font-family: sans-serif;
    margin: 0px;
    margin-top: 5px;
    margin-bottom: 10px;
    padding: 0px;
}

.icons {
    width: 100%;
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-start;
}

.icon:hover {
    cursor:pointer;
}

.join {
    height: 400px;
    background: #ededed;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.callout-inner-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.join_button_big {
    height: 90px;
    width: 300px;
    background:#0A6EAC;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 30px;
    color: white;
    
    font-size: 20px;
    margin-top: 20px;
    font-weight: bold;
    letter-spacing: 4px;
    transition: background .25s ease-in-out;
    -moz-transition: background 0.25s ease-in-out;
    -webkit-transition: background 0.25s ease-in-out;
    -o-transition: background 0.25s ease-in-out;
    -ms-transition: background 0.25s ease-in-out;
    text-decoration: none !important;
}

.join_button_big:hover {
    background: #00A2FF;
    cursor: pointer;
    text-decoration: none !important;


}