*{
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}
.table-row {
    align-self: center;
    margin: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 0px;
    width: 90%;
  
    background: white;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.table-row:hover{
    cursor: pointer;
}

.title-block {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 10px;
    background: #EBD99F;
    width: 100%;
    
  
    
}


.title {
    font-size: 28px;

}
.always-shown {
    width: 100%;

}
.contact {
    color: black;
    font-size: 18px;
}
.contact a {
    text-decoration: none;
    color: rgb(136, 136, 136) !important;
    font-size: 18px;
}



.data-title {
    font-weight: bold;
}

.inner-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    background: rgb(205, 205, 205);
    width: 100%;
    
}
.data-item {
    background:rgb(205, 205, 205);
    width: 100%;
    padding: 10px;
}

.data-item:nth-child(even) {
    background: white;
}

.website {
    flex-wrap: wrap !important;
}

@media only screen and (max-width: 1150px) {
    .data-item {
        flex-wrap: wrap;
    }
    .table-row {
        width: 100%;
    }
}

* {

    box-sizing: border-box;
    padding: 0px;
    margin: 0px;

}

table, th, td {
    
    border: 2px solid rgb(151, 151, 151);
    border-collapse: collapse;
    background: white;
}

.table-controls {
    height: 50px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

}

.table-title {
    font-size: 25px;
    font-weight: bold;
}

.filter-button-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    height: 50px !important;
    
    
}
.filter-button {
    font-size: 18px;
    
    margin: 0px;
    padding: 0px;
    
    
}

.filter {
    
    position: fixed;
    margin-top: 10px;
    /* height: 250px;
    width: 450px; */
    height: 450px;
    width: 650px;
    max-width: 80%;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}
.filter-body {
    flex-grow: 1;
    width: 100%;
    background: yellow;
}
.bottom-filter-controls {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    height: 60px;
    width: 100%;
    padding-right: 20px;
}

.close-button {
    margin: 5px !important;
    background: rgb(243, 243, 243) !important;
    color: rgb(190, 11, 11) !important;
    font-weight: bold !important;
}
.apply-button {
    margin: 5px !important;
    background: rgb(50, 50, 134) !important;
    color: white !important;
    font-weight: bold !important;
}
.custom-table {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    background: white;

    overflow-x: auto;
    
}

.custom-table-row {
    height: 50px;
    border-bottom: 2px solid rgb(153, 153, 153);

    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;

}

.header {
    font-weight: bold;
    background: yellow;

}

.column {
    border-right: 1px solid rgb(153, 153, 153);
    border-left: 1px solid rgb(153, 153, 153);
    height: 100%;
    min-width: 300px;
}

.last {
    flex-grow: 1;
}

