/* //use *fixed* in background to keep image behind and static */

@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap');


.home-background {
    position: relative;
    box-sizing: border-box;
    background: url(../images/home.svg) no-repeat right bottom ; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    height: 100%;
    min-height: 100vh;
    width: 100%;
    min-width: 100%;
    
}

.pspe_title_text {
    font-family: 'Open Sans', sans-serif;

}

.pspe_title {
    font-family: 'Open Sans', sans-serif;
    width: 80%;
    position: relative;
    top: 25px;
    left: 25px;
    color: white;
    font-size: 48px;
}

.notification_button {

}

.notification_button a:hover {
    text-decoration: none;
}
.notification_button_group {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    padding-left: 25px;
    margin-top: 50px;
}

.title_gold {
    color: #CFB991;
    font-weight: 600;
    font-family: 'Open Sans', sans-serif;
    
}

h4 {
    font-size: 25px;
    font-family: 'Open Sans', sans-serif;
}
.title_black {
    color: rgb(50, 50, 50);
    font-family: 'Open Sans', sans-serif;
}


.transition_to_content {
    background: #2b2b2b;
    width: 100%;
    height: 300px;
    padding: 25px;
    box-sizing: border-box;
}
.events_home {
    display: flex;
    flex-direction: column;
    color: #C4BFC0;
    
}
.uppercase {
    text-transform: uppercase;
}

.home_event {
    
}

.event_date {
    font-size: 22px;
    font-weight: 700;
    margin-right: 10px;
}
.event_title {
    font-size: 20px;
}
.announcements_home {
    color: rgba(235, 217, 159);
    display: flex;
    flex-direction: column;
}

.home_announcement {
    display: flex;
    flex-direction: column;
    color: #C4BFC0;

}
.announcement_title {
    font-weight: 700;
    font-size: 20px;
}
.announcement_description {

}
.home_content {
    width: 100%;
    height: 500px;
}
@media only screen and (max-width: 580px) {
    .pspe_title {
        font-size: 35px;
    }
    .notification_button {
        font-size: 14px;
        margin-bottom: 15px;
    }
    .notification_button_group {
        flex-direction: column;

    }

}


