*{
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}
.table-row {
    align-self: center;
    margin: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 0px;
    width: 90%;
  
    background: white;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.table-row:hover{
    cursor: pointer;
}

.title-block {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 10px;
    background: #EBD99F;
    width: 100%;
    
  
    
}


.title {
    font-size: 28px;

}
.always-shown {
    width: 100%;

}
.contact {
    color: black;
    font-size: 18px;
}
.contact a {
    text-decoration: none;
    color: rgb(136, 136, 136) !important;
    font-size: 18px;
}



.data-title {
    font-weight: bold;
}

.inner-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    background: rgb(205, 205, 205);
    width: 100%;
    
}
.data-item {
    background:rgb(205, 205, 205);
    width: 100%;
    padding: 10px;
}

.data-item:nth-child(even) {
    background: white;
}

.website {
    flex-wrap: wrap !important;
}

@media only screen and (max-width: 1150px) {
    .data-item {
        flex-wrap: wrap;
    }
    .table-row {
        width: 100%;
    }
}
